<template>
    <div class="navigation">
        <transition name="fade">
            <div class="overlay" v-if="show" @click="toggle"></div>
        </transition>
        <div class="btn" :class="{canshow:!show}">
                <div class="icon" @click="toggle" :class="{canshow:!show}">
                    <i :class="nowIcon"></i>
                </div>
            <transition name="pop">
            <div v-if="show" class="wrapper">
                <ul>
                    <li v-for="item in initList">
                        <a href="javascript:;" @click="playlist" v-if="item==='playlist'">
                            <i class="icon-sound-mix"></i>
                        </a>
                        <a href="javascript:;" @click="profile" v-else-if="item==='profile'">
                            <i class="icon-profile"></i>
                        </a>
                        <a href="javascript:;" @click="player" v-else-if="item==='player'">
                            <i class="icon-song"></i>
                        </a>
                        <a href="javascript:;" @click="music" v-else-if="item==='mymusic'">
                            <i class="icon-listen"></i>
                        </a>
                    </li>
                </ul>
            </div>
            </transition>
        </div>    
    </div>
  
</template>
<script>
export default {
    props: {
        nowpath:{
            type:String,
        }
    },
    data () {
        return {
            show:false,
            route:['playlist','player','profile','mymusic']
        }
    },
    computed: {
      initList(){
          var newRoute = []
          for(var i=0;i<this.route.length;i++){
              if(this.nowpath === this.route[i]){
                continue;
              }
              newRoute.push(this.route[i])
          }
          return newRoute;
      },
      nowIcon(){
            if(this.nowpath === 'playlist'){
                return 'icon-sound-mix';
            }
            else if(this.nowpath === 'player'){
                return 'icon-song';
            }
            else if(this.nowpath === 'profile'){
                return 'icon-profile';
            }
            else if(this.nowpath === 'mymusic'){
                return 'icon-listen';
            }
        },
    },
    methods: {
        toggle(){
            this.show = !this.show;
        },
        playlist(){
            this.toggle();
            this.$router.push('/playlist')
        },
        player(){
            this.toggle();
            this.$router.push('/player')
        },
        profile(){
            this.toggle();
            this.$router.push('/profile')
        },
        music(){
            this.toggle();
            this.$router.push('/mymusic')
        }
    }
}
</script>

<style lang="scss">
    .navigation{
        .fade-enter-active,.fade-leave-active{
            transition: all 0.5s;
        }
        .fade-enter,.fade-leave-active{
            opacity: 0;
        }
        .overlay{
            position: absolute;
            top: 0;
            left: 0;
            overflow: hidden;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,0.7);
        }
        .btn{
            position: absolute;
            bottom:0;
            right: 0;
            width: 150px;
            height: 150px;
            text-align: center;
            color: #fff;
            border-top-left-radius: 150px;
            overflow: hidden;
            &.canshow{
                width: 70px;
                height: 70px;
                border-top-left-radius: 150px;
                transition: all 0.8s;
            }
            .wrapper{
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-top-left-radius: 150px;
            }
            .pop-enter-active,.pop-leave-active{
                transition: all 0.8s;
            }
            .pop-enter,.pop-leave-active{
                transform: scale(0);
                transform-origin: 100% 100%;
            }
            .icon{
                position: absolute;
                right: 0;
                bottom: 0;
                width: 70px;
                height: 70px;
                line-height: 70px;
                font-size: 22px;
                text-align: center;
                background: #fff;
                border-top-left-radius: 70px;
                z-index: 100;
                color: #f06060;
                transition: all 0.3s;
                &.canshow{
                    background: rgba(255,255,255,0.3) !important;
                    transition: all 5s;
                }
                i{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                }
            }
            
            ul{
                width: 100%;
                height:100%;
                // transform-origin: 100% 100%;
                li{
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    width: 150px;
                    height: 150px;
                    
                    transform-origin: 100% 100%;
                    overflow: hidden;
                    background: #901911;
                    &:nth-child(1){
                            background-color: hsla(0, 88%, 63%, 1);
                        transform: rotate(0) skew(60deg);
                    }
                    &:nth-child(2){
                        background-color: hsla(0, 88%, 65%, 1);
                        transform: rotate(30deg) skew(60deg);
                    }
                    &:nth-child(3){
                        background-color: hsla(0, 88%, 63%, 1);
                        transform: rotate(60deg) skew(60deg);
                    }
                    a{
                        position: absolute;
                        top:0;
                        left:0;
                        display: block;
                        width: 300px;
                        height: 300px;
                        padding-top: 18px;
                        font-size: 20px;
                        transform: skew(-60deg) rotate(-73deg) scale(1);
                        text-align: center;
                        color: #fff;
                    }
                }
            }
        }
    }
</style>




